import React, { useState, useEffect } from 'react';

export function Contact(){
    return(
        <div className='Contact'>
            <CallToAction/>
            <p>Mike Peterson</p>
            <a href="mailto:mike@lemonshell.com">mike@lemonshell.com</a>
            <br/>
            <a href="tel:712-204-7392">712 - 204 - 7392</a>
            <br/>
            <Vcard/>
            <br/>
            <a className='Resume' href="./Mike Peterson's Resume.pdf">Resume</a>
        </div>
    )
}

function CallToAction(){
    const descList = ['AWESOME', 'REACTIVE', 'UNIQUE', 'INSPIRING', 'REVOLUTIONARY', 'EXCITING']
    let [desc, setDesc] = useState(0)

    useEffect(() => {
        setTimeout(() => {  
            setDesc(desc+1)
        }, 1500);
    });
    
    return(
        <div className='CallToAction'>
            <h1>Let's make something <span>{descList[desc%descList.length]}!</span></h1>
        </div>
    )
}

export function Vcard(props){
    let [href, setHref] = useState('')

    let text = "BEGIN:VCARD\n" +
                "VERSION:3.0\n" +
                "N:Peterson;Mike;;;\n" +
                "FN:Mike Peterson\n" +
                "ORG:Lemonshell;\n" +
                "TITLE:Web Developer\n" +
                "EMAIL;type=pref:mike@lemonshell.com\n" +
                "TEL;type=CELL:712-204-7392\n" +
                
                "item3.X-ABLabel:_$!<HomePage>!$_\n" +
                "item4.URL:http\://lemonshell.com\n" +
                "END:VCARD"

    let data = new Blob([text], { type: 'text/vcard' });
    let url = window.URL.createObjectURL(data);

    useEffect(() => {if (href === '') setHref(url)});

    return <a className="Vcard" download="MikePeterson.vcf" href={href}> Save to Contacts </a>
}
