import React, { useState } from 'react';
import {Repos} from './Repos'
import {Contact} from './Contact'




// #### PAGES ####

export function Pages(){
    let pages = {
        'Projects':<Repos github='kid-on-github'/>,
        'Contact':<Contact/>,
    }

    let [page, loadPage] = useState('Projects')

    return(
        <div className='Pages'>
            <Tabs tabs={Object.keys(pages)} loadPage={loadPage} selectedTab={page}/>
            <Content page={pages[page]}/>
        </div>
    )
}




// #### TABS ####

function Tabs(props){
    let tabs = props.tabs.map(
        (tab,i) => {
            let highlight = tab === props.selectedTab ? 'true' : 'false'
            return <button onClick={()=>props.loadPage(tab)} highlight={highlight} key={tab+'-'+i}>{tab}</button>
        }
    )

    return (
        <div className='Tabs'>
            <span className='content'>
                {tabs}
            </span>
        </div>
    )
}




// #### CONTENT ####

function Content(props){
    return <div className='Page'>{props.page}</div>
}




